@import '../variables.scss';

.main-header {
    padding: 0 $side-padding;
    height: 185px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: all 0.3s ease-in-out;
    box-shadow: 0px -3px 14px 3px #c5c5c5;
    @media only screen and (max-width: 1023px) {
        height: 90px;
    }
    @media only screen and (max-width: 767px) {
        height: 62px;
    }
    .logo-nav {
        height: 130px;
        transition: all 0.3s ease-in-out;
        @media only screen and (max-width: 1023px) {
            height: 50px;
        }
        @media only screen and (max-width: 767px) {
            height: 42px;
        }
        img {
            height: 100%;
            display: block;
        }
    }
    nav {
        display: flex;
        align-items: center;
        ul {
            display: flex;
            flex-direction: row;
            font-size: $fs-paragraph;
            align-items: center;
            li {
                margin-left: 36px;
                @media only screen and (max-width: 767px) {
                    margin-left: 20px;
                }
                a {
                    cursor: pointer;
                    border-radius: 3px;
                    @media only screen and (max-width: 767px) {
                        height: 40px;
                        font-size: 10px;
                    }
                }
            }
        }
    }
}